<template>
    <div>
        <el-table
                :data="timetable"
                :span-method="objectSpanMethod"
                :cell-style="cellDisplay"
                :header-cell-style="{color: 'black', background: '#f6f7fa'}"
                @cell-click="SelectOperation"
                border
                style="width: 100%; margin-top: 20px; color: black"
        >
            <el-table-column prop="period" label="" header-align="center" width="60"/>
            <el-table-column prop="monday" label="" header-align="center" :formatter="infoDisplay"/>
            <el-table-column prop="tuesday" label="" header-align="center"  :formatter="infoDisplay"/>
            <el-table-column prop="wednesday" label="" header-align="center"  :formatter="infoDisplay"/>
            <el-table-column prop="thursday" label="" header-align="center"  :formatter="infoDisplay"/>
            <el-table-column prop="friday" label="" header-align="center"  :formatter="infoDisplay"/>
            <el-table-column prop="saturday" label="" header-align="center"  :formatter="infoDisplay"/>
            <el-table-column prop="sunday" label="" header-align="center"  :formatter="infoDisplay"/>
        </el-table>

        <!--  -->
        <el-dialog
                v-model="editMode"
                style="border-radius: 20px"
                title=""
                draggable
                :modal="false">
            <el-form ref="editForm" :model="editClass" :rules="classRules">
                <el-form-item label="" prop="title">
                    <el-input v-model="editClass.title" style="width: 300px"/>
                </el-form-item>

                <el-form-item label="" prop="location">
                    <el-input v-model="editClass.location" style="width: 300px"/>
                </el-form-item>

                <el-row>
                    <el-col :span="8">
                        <!--                      -->
                        <el-form-item label="" prop="weekday">
                            <el-select placeholder="" v-model="editClass.weekday" style="width: 100px">
                                <el-option
                                        v-for="item in weekdaysOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <!--                      -->
                        <el-form-item label="" prop="start">
                            <el-select placeholder="" v-model="editClass.start" style="width: 100px">
                                <el-option
                                        v-for="item in periodOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                        :disabled="DisabledStartTime(item.value)"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <!--                      -->
                        <el-form-item label="" prop="end">
                            <el-select placeholder="" v-model="editClass.end" style="width: 100px">
                                <el-option
                                        v-for="item in periodOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                        :disabled="DisabledEndTime(item.value)"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="">
                    <el-color-picker
                            v-model="editClass.color"
                            color-format='hex'
                            :predefine="predefineColors"/>
                </el-form-item>
            </el-form>

            <template #footer>
              <span class="dialog-footer">
                  <el-button type="success" :icon="Check" circle @click="SaveEditChange"/>
                  <el-button type="danger" :icon="Delete" circle @click="DeleteClass"/>
                  <el-button :icon="Close" circle @click="QuitEditChange"/>
              </span>
            </template>
        </el-dialog>

        <!--  -->
        <el-dialog
                v-model="addMode"
                style="border-radius: 20px"
                title=""
                draggable
                :modal="false">
            <el-form ref="addForm" :model="editClass" :rules="classRules">
                <el-form-item label="" prop="title">
                    <el-input v-model="editClass.title" style="width: 300px"/>
                </el-form-item>

                <el-form-item label="" prop="location">
                    <el-input v-model="editClass.location" style="width: 300px"/>
                </el-form-item>

                <el-row>
                    <el-col :span="8">
                        <!--                      -->
                        <el-form-item label="" prop="weekday">
                            <el-select placeholder="" v-model="editClass.weekday" style="width: 100px">
                                <el-option
                                        v-for="item in weekdaysOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <!--                      -->
                        <el-form-item label="" prop="start">
                            <el-select placeholder="" v-model="editClass.start" style="width: 100px">
                                <el-option
                                        v-for="item in periodOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                        :disabled="DisabledStartTime(item.value)"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <!--                      -->
                        <el-form-item label="" prop="end">
                            <el-select placeholder="" v-model="editClass.end" style="width: 100px">
                                <el-option
                                        v-for="item in periodOptions"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value"
                                        :disabled="DisabledEndTime(item.value)"
                                />
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="">
                    <el-color-picker
                            v-model="editClass.color"
                            color-format='hex'
                            :predefine="predefineColors"/>
                </el-form-item>
            </el-form>

            <template #footer>
              <span class="dialog-footer">
                  <el-button type="success" :icon="Check" circle @click="SaveAddChange"/>
                  <el-button :icon="Close" circle @click="QuitAddChange"/>
              </span>
            </template>
        </el-dialog>

    </div>
</template>

<script>
import {Check, Close, Delete} from "@element-plus/icons-vue";
import request from "@/utils/request";
import {watch} from "vue";
import router from "@/router";

export default{
    name: "timetable",

    computed: {
        Delete() {
            return Delete
        },
        Close() {
            return Close
        },
        Check() {
            return Check
        }
    },

    inject: ['isNightMode'],

    props: {
        // 
        classNumPerDay: {
            type: [String, Number],
            default: 12
        }
    },

    methods: {
        load(){
            let userJson = sessionStorage.getItem("user")
            const temporaryUser = JSON.parse(userJson)
            request.get("/api/userCenter/timetable", {
                params: {
                    search: temporaryUser.uid
                }
            }).then(res => {
                this.classes = JSON.parse(JSON.stringify(res.data))
                this.mergeData()
            })
        },
        // 
        makeTimetable(){
            this.timetable = []
            for(let i = 0; i < this.classNumPerDay; ++i){
                let one = {
                    period: i + 1,
                    monday: {},
                    tuesday: {},
                    wednesday: {},
                    thursday: {},
                    friday: {},
                    saturday: {},
                    sunday: {}
                }
                this.timetable.push(one)
            }
        },
        // , truefalse
        makeAvailablePeriods(){
            this.availablePeriods=[]
            for(let i = 0; i < this.classNumPerDay; ++i){
                let one = {
                    monday: true,
                    tuesday: true,
                    wednesday: true,
                    thursday: true,
                    friday: true,
                    saturday: true,
                    sunday: true
                }
                this.availablePeriods.push(one)
            }
        },
        // classes
        mergeData(){
            this.makeTimetable()
            this.makeAvailablePeriods()
            if(this.classes.length > 0) {
                for(let i = 0; i < this.classes.length; i++){
                    let weekday = this.weekdays[this.classes[i].weekday - 1]
                    this.timetable[this.classes[i].start - 1][weekday] = this.classes[i]
                    for(let t = this.classes[i].start - 1; t < this.classes[i].end; ++t)
                        this.availablePeriods[t][weekday] = false
                }
            }
        },
        // 
        objectSpanMethod({rowIndex, columnIndex}){
            //
            if(columnIndex === 0)
                return{
                    rowspan: 1,
                    colspan: 1
                }
            let rowSpan = 1, colSpan = 1
            let weekday = this.weekdays[columnIndex - 1]
            let currentClass = this.timetable[rowIndex][weekday]
            if(currentClass.title !== undefined){
                rowSpan = currentClass.end - currentClass.start + 1
                colSpan = 1
            }
            else if (this.availablePeriods[rowIndex][weekday] === false){
                rowSpan = 0
                colSpan = 0
            }
            else{
                rowSpan = 1
                colSpan = 1
            }
            return{
                rowspan: rowSpan,
                colspan: colSpan
            }
        },
        // 
        infoDisplay(row, column){
            let value = row[column.property]
            if(value.title !== undefined)
                return `${value.title}[${value.location}]`
        },
        // 
        cellDisplay({row,column}){
            let value = row[column.property]
            if (value.title !== undefined)
                return {
                    background: value.color,
                    textAlign: 'center',
                };
            else return {
                background: this.backgroundColor,
                textAlign: 'center',
                color: this.textColor
            }
        },
        // or
        SelectOperation(row, column){
            if(column.property === "period")
                return
            let value = row[column.property]
            if (value.title !== undefined) {
                this.editMode = true

                for(let i = 0; i < this.classes.length; i++){
                    if(this.classes[i] === row[column.property]) {
                        // 
                        this.editIndex = i
                        this.editClass = JSON.parse(JSON.stringify(this.classes[i]))
                        // 
                        let weekday = this.weekdays[this.editClass.weekday - 1]
                        for(let t = this.editClass.start - 1; t < this.editClass.end; ++t)
                            this.availablePeriods[t][weekday] = true
                        break
                    }
                }
            }
            else {
                let userJson = sessionStorage.getItem("user")
                const temporaryUser = JSON.parse(userJson)
                this.addMode = true
                this.editClass = {
                    weekday: 1,
                    uid: temporaryUser.uid
                }
                this.editIndex = -1
            }
        },
        // 
        SaveEditChange(){
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    request.put("/api/userCenter/timetable", this.editClass).then(res => {
                        if (res.code === '0'){
                            this.$message({
                                type: "success",
                                message: ""
                            })
                            this.classes[this.editIndex] = JSON.parse(JSON.stringify(this.editClass))
                            this.mergeData()
                            this.editClass = {}
                            this.editMode = false
                            this.dialogVisible = false
                        } else {
                            this.$message({
                                type: "error",
                                message: res.msg
                            })
                        }
                    })
                } else {
                    console.log('');
                    return false;
                }
            });
        },
        // 
        QuitEditChange(){
            this.editClass = {}
            this.editMode = false
            // 
            let weekday = this.weekdays[this.classes[this.editIndex].weekday - 1]
            for(let t = this.classes[this.editIndex].start - 1; t < this.classes[this.editIndex].end; ++t)
                this.availablePeriods[t][weekday] = false
        },
        // 
        DeleteClass(){
            request.delete("/api/userCenter/timetable",{
                params: {
                    cid: this.editClass.cid
                }
            }).then(res => {
                if (res.code === '0'){
                    this.$message({
                        type: "success",
                        message: ""
                    })
                    this.classes.splice(this.editIndex, 1)
                    this.mergeData()
                    this.editClass = {}
                    this.editMode = false
                } else {
                    this.$message({
                        type: "error",
                        message: res.msg
                    })
                }
            })
        },
        // 
        DisabledStartTime(period){
            if(typeof this.editClass.weekday === 'undefined')
                return false
            const weekday = this.weekdays[this.editClass.weekday - 1]
            if(this.availablePeriods[period - 1][weekday] === false)
                return true
            else if(this.editIndex !== -1 && this.editClass.weekday === this.classes[this.editIndex].weekday) {
                for(let i = period; i < this.editClass.end; ++i){
                    if (this.availablePeriods[i - 1][weekday] === false)
                        return true
                }
                return period > this.editClass.end
            }
            else if(this.editIndex === -1) {
                for(let i = period; i < this.editClass.end; ++i){
                    if (this.availablePeriods[i - 1][weekday] === false)
                        return true
                }
                return period > this.editClass.end
            }
            else
                return false
        },
        // 
        DisabledEndTime(period){
            if(typeof this.editClass.weekday === 'undefined')
                return false
            const weekday = this.weekdays[this.editClass.weekday - 1]
            if(this.availablePeriods[period - 1][weekday] === false)
                return true
            else if(this.editIndex !== -1 && this.editClass.weekday === this.classes[this.editIndex].weekday) {
                for(let i = period; i > this.editClass.start; --i){
                    if (this.availablePeriods[i - 1][weekday] === false)
                        return true
                }
                return period < this.editClass.start
            }
            else if(this.editIndex === -1) {
                for(let i = period; i > this.editClass.start; --i){
                    if (this.availablePeriods[i - 1][weekday] === false)
                        return true
                }
                return period < this.editClass.start
            }
            else
                return false
        },
        // 
        SaveAddChange(){
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    request.post("/api/userCenter/timetable", this.editClass).then(res => {
                        if (res.code === '0'){
                            this.$message({
                                type: "success",
                                message: ""
                            })
                            this.load()
                            this.editClass = {}
                            this.addMode = false
                        } else {
                            this.$message({
                                type: "error",
                                message: res.msg
                            })
                        }
                    })
                } else {
                    console.log('');
                    return false;
                }
            })
        },
        // 
        QuitAddChange(){
            this.editClass = {}
            this.addMode = false
        },
        // 
        nightModeChanged(){
            if(this.isNightMode.value) {
                this.backgroundColor = '#1f2938'
                this.textColor = 'white';
            }
            else{
                this.backgroundColor = 'white'
                this.textColor = 'black';
            }
        }
    },

    data() {
        return {
            // 
            classes:[
                {
                    weekday: 1,
                    title: '',
                    location: '',
                    start: 1,
                    end: 3,
                    color: '#FEE4CB'
                },
                {
                    weekday: 2,
                    title: '',
                    location: '',
                    start: 4,
                    end: 5,
                    color: '#E9E7FD'
                }
            ],
            // 
            editClass: {},
            classRules:{
                title: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                location: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                weekday: [
                    { required: true, message: '', trigger: 'change' },
                ],
                start: [
                    { required: true, message: '', trigger: 'change' },
                ],
                end: [
                    { required: true, message: '', trigger: 'change' },
                ]
            },
            editIndex: 0,
            editMode: false,
            addMode: false,
            // 
            timetable: [],
            availablePeriods: [],
            weekdays: ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'],
            // 
            weekdaysOptions: [
                {
                    value: 1,
                    label: ''
                },
                {
                    value: 2,
                    label: ''
                },
                {
                    value: 3,
                    label: ''
                },
                {
                    value: 4,
                    label: ''
                },
                {
                    value: 5,
                    label: ''
                },
                {
                    value: 6,
                    label: ''
                },
                {
                    value: 7,
                    label: ''
                },
            ],
            periodOptions:[
                {
                    value: 1,
                    label: '',
                },
                {
                    value: 2,
                    label: '',
                },
                {
                    value: 3,
                    label: '',
                },
                {
                    value: 4,
                    label: '',
                },
                {
                    value: 5,
                    label: '',
                },
                {
                    value: 6,
                    label: '',
                },
                {
                    value: 7,
                    label: '',
                },
                {
                    value: 8,
                    label: '',
                },
                {
                    value: 9,
                    label: '',
                },
                {
                    value: 10,
                    label: '',
                },
                {
                    value: 11,
                    label: '',
                },
                {
                    value: 12,
                    label: '',
                },
            ],
            predefineColors: [
                '#FEE4CB',
                '#E9E7FD',
                '#DBF6FD',
                '#FFD3E2',
                '#C8F7DC',
                '#D5DEFF'
            ],
            // 
            backgroundColor: 'white',
            textColor: 'black'
        }
    },

    created() {
        let userJson = sessionStorage.getItem("user")
        if(!userJson) {
            router.push("/login")
        }
        this.load()
    },

    mounted() {
        watch(
            () => this.isNightMode.value,
            (newValue, oldValue) => {
                this.nightModeChanged();
            }
        );
        if(this.isNightMode.value){
            this.nightModeChanged();
        }
    },
}
</script>

